<template>
  <div class="song" v-if="songSheet">
    <div class="list " v-for="item in songSheet" :key="item.id" @click="toDetail(item.id)">
      <img :src="item.picUrl || item.coverImgUrl" alt="" />
      <div class="ellipsis-two ">{{ item.name }}</div>
      <div class="icon"><img src="@/assets/img/recommend/erji.svg" alt="" />{{ item.playCount ? item.playCount : item.type }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['songSheet'],
  methods: {
    toDetail(id) {
      this.$router.push({
        path: '/playlistdetail',
        query: {
          id
        }
      })
    }
  }
}
</script>

