import { axios } from './axios'
// 歌单分类
export function getCatlist() {
  return axios({
    url: '/playlist/catlist'
  })
}
// 热门歌单分类
export function getHotCatlist() {
  return axios({
    url: '/playlist/hot'
  })
}
// 歌单精选
export function getTopCatlist(params) {
         return axios({
           url: '/top/playlist',
           params: {
             order: params.order,
             cat: params.cat,
             limit: params.limit,
             offset:params.offset
           }
         })
       }
