<template>
  <div class="song limit">
    <div class="title">
      <div class="all-tag" @click="openTags">
        全部
        <i class="icon el-icon-arrow-down"></i>
        <transition name="fate">
          <div class="tags" v-if="showTags">
            <div class="item" v-for="item in sorted" :key="item.category">
              <h2><i class="iconfont" :class="item.icon"></i>{{ item.type }}</h2>
              <ul>
                <li v-for="item1 in item.list" :key="item1.name" @click="shooseCat(item1.name)" :class="{ active: currentTag === item1.name }">{{ item1.name }}</li>
              </ul>
            </div>
          </div>
        </transition>
      </div>
      <div class="hot-tags">
        <div class="hot-title">
          热门标签 ：
          <ul class="item">
            <li v-for="item in hotTags" :key="item.id" @click="shooseCat(item.name)" :class="{ active: currentTag === item.name }">{{ item.name }}</li>
          </ul>
        </div>
      </div>
      <div class="type-tag">
        <div class="hot" @click="chooseType('hot')"><span :class="{ active: sortType === 'hot' }">热门</span></div>
        <div class="new" @click="chooseType('new')"><span :class="{ active: sortType === 'new' }">最新</span></div>
      </div>
    </div>
    <song-sheet :songSheet="allSongSheet"></song-sheet>
    <el-pagination class="asd"  @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="limit" layout="total, prev, pager, next" :total="total"> </el-pagination>
    <loading v-show="isLoading"></loading>

  </div>
</template>

<script>
import songSheet from '@/components/common/songSheet/songSheet'
import { getCatlist, getHotCatlist, getTopCatlist } from '@/network/songsheet'
export default {
  components: {
    songSheet
  },
  data() {
    return {
      hotTags: [],
      showTags: false,
      isLoading:true,
      typeList: [
        {
          key: 0,
          value: '语种',
          icon: 'icon-yuzhong'
        },
        {
          key: 1,
          value: '风格',
          icon: 'icon-fengge'
        },
        {
          key: 2,
          value: '场景',
          icon: 'icon-changjing'
        },
        {
          key: 3,
          value: '情感',
          icon: 'icon-qinggan'
        },
        {
          key: 4,
          value: '主题',
          icon: 'icon-zhuti'
        }
      ],
      sorted: [],
      allSongSheet: [],
      total: 0,
      sortType: 'hot',
      limit: 45,
      offset: 0,
      currentTag: '全部',
      currentPage: 0
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
    
      this.getTopCatlist()
      this.getCatlist()
      this.getHotCatlist()
    },
    getTopCatlist() {
      let params = {
        order: this.sortType,
        cat: this.currentTag,
        limit: this.limit,
        offset: this.offset
      }
      getTopCatlist(params).then(res => {
        this.allSongSheet = res.data.playlists
        this.total = res.data.total
        console.log(this.allSongSheet)
      this.isLoading=false

      })
    },
    // 获得歌单
    getCatlist() {
      getCatlist().then(res => {
        this.sorted = this.groupBy(res.data.sub, function(item) {
          return item.category
      this.isLoading=false

        })
      })
    },
    // 获得热门标签
    getHotCatlist() {
      getHotCatlist().then(res => {
        this.hotTags = res.data.tags
      this.isLoading=false

      })
    },
    // 数组分类方法
    groupBy(array, f) {
      let groups = {}

      array.forEach(function(o) {
        let group = f(o)
        groups[group] = groups[group] || []
        groups[group].push(o)
      })
      // 合并数组
      let arr = []
      for (let asd in groups) {
        let type = ''
        let category = ''
        let icon = ''
        this.typeList.forEach(item => {
          if (item.key == asd) {
            type = item.value
            category = item.key
            icon = item.icon
          }
        })
        arr.push({
          type,
          category,
          icon,
          list: groups[asd]
        })
      }

      return Object.values(arr)
    },
    openTags() {
      this.showTags = !this.showTags
    },
    chooseType(type) {
      this.sortType = type
      this.getTopCatlist()
    },
    // 选择标签
    shooseCat(type) {
      this.currentTag = type
      this.getTopCatlist()
    },
    // 改变页数
    handleCurrentChange(index) {
      this.offset = (index - 1) * this.limit
      this.currentPage = index
      this.getTopCatlist()
    }
  }
}
</script>

<style scoped lang="less">
.song {
  margin: 30px auto;
  border-radius: 8px;
  .title {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 39px;
    background-color: #fff;
    .all-tag {
      z-index: 999;
      text-align: center;
      padding-left: 15px;
      color: #fff;
      line-height: 39px;
      padding: 0 10px;
      background-color: #e5a593;
      width: 100px;
      border-radius: 5px;
      .icon {
        margin-left: 10px;
      }
      .tags {
        margin: 10px 0 0 -12px;
        width: 720px;
        height: 400px;
        color: #000;
        background-color: #eee;
        border-radius: 5px;
        overflow-y: scroll;
        .item {
          margin-left: 20px;
          h2 {
            color: #161e27;
            display: flex;
            font-size: 16px;
            i {
              font-size: 18px;
              margin-right: 8px;
            }
          }
          ul {
            display: flex;
            flex-wrap: wrap;

            li {
              padding: 0 18px;
              background: #f7f7f7;
              border-radius: 50px;
              margin: 0 10px 10px 0;
              cursor: pointer;
              font-size: 12px;
              color: #161e27;
            }
            li:hover {
              color: #fff;
              background-color: #fa2800;
            }
          }
        }
      }
      //   隐藏滚动条
      .tags::-webkit-scrollbar {
        display: none;
      }
    }
    .hot-tags {
      line-height: 39px;
      display: flex;
      flex: 1;
      font-size: 15px;
      font-weight: 600;
      .hot-title {
        display: flex;
        margin: auto 15px;
        .item {
          display: flex;
          font-size: 14px;
          font-weight: 500;

          li {
            padding: 0 8px;
            line-height: 28px;
            margin: auto 10px;
            cursor: pointer;
            border-radius: 10px;
          }
          li:hover {
            color: #fa2800;
          }
        }
      }
    }
    .type-tag {
      display: flex;
      font-size: 14px;
      align-items: center;
      .hot {
        height: 100%;
        margin-right: 20px;
        span {
          line-height: 39px;
          padding: 6px;
          font-size: 12px;
          background-color: #eee;
          border-radius: 5px;
        }
      }
      .new {
        margin-right: 20px;
        span {
          line-height: 39px;
          padding: 6px;
          font-size: 12px;
          background-color: #eee;
          border-radius: 5px;
        }
      }
    }
  }
  .active {
    background-color: #fa2800 !important;
    color: #fff;
  }
  .active:hover {
    color: #000 !important;
  }
  .asd{
    display: flex;
    justify-content: center;
  }
}
</style>
